<template>
  <div>
    <ContenidoEmisionEspecifico :emisiones="emisionesEstrenos" tipo="Estrenos" />
    <ContenidoEmisionEspecifico :emisiones="emisionesSeries" tipo="Series" />
    <ContenidoEmisionEspecifico :emisiones="emisionesContemporaneas" tipo="Contemporaneas" />
  </div>
</template>

<script>
import ContenidoEmisionEspecifico from "@/components/ContenidoEmisionComp/ContenidoEmisionEspecifico.vue";
import { mapMutations, mapGetters, mapActions } from "vuex";

export default {
  name: "ContenidoEmision",
  components: {
    ContenidoEmisionEspecifico
  },
  computed: {
    ...mapGetters("emisiones", [
      "emisionesEstrenos",
      "emisionesSeries",
      "emisionesContemporaneas"
    ])
  },
  methods: {
    ...mapMutations(["addBreadcrumb"]),
    ...mapActions("emisiones", ["loadEmisionesTipo"])
  },
  async created() {
    for (let i = 0; i <= 2; i++) {
      await this.loadEmisionesTipo(i);
    }
    this.addBreadcrumb([
      {
        text: "Home",
        to: { name: "Home" }
      },
      {
        text: "Contenido en Emision",
        to: { name: "Contenido en Emision" }
      }
    ]);
  }
};
</script>

