<template>
  <div>
    <b-modal
      id="gestionEmision"
      v-model="gestionEmision"
      :title="modo + ' Contenido en Emision'"
    >
      <b-card
        body-class="text-center"
        header-tag="nav"
      >
        <ListaContenido :contenidos="catalogo" />
      </b-card>
      <template v-slot:modal-footer="{ ok, cancel }">
        <b-button
          v-if="modo === 'Agregar'"
          size="sm"
          variant="success"
          @click="
            ok();
            agregarEmision();
          "
        >
          Agregar
        </b-button>
        <b-button
          v-else
          size="sm"
          variant="success"
          @click="
            ok();
            modificarEmision();
          "
        >
          Modificar
        </b-button>
        <b-button
          size="sm"
          variant="danger"
          @click="cancel()"
        >
          Cancelar
        </b-button>
      </template>
    </b-modal>
    <b-navbar
      toggleable="lg"
      type="light"
      variant="light"
    >
      <b-navbar-brand
        tag="h1"
        class="mb-0"
      >{{ tipo }}</b-navbar-brand>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-navbar-nav class="ml-auto">
        <b-button-group>
          <b-button
            variant="primary"
            @click="cargarContenido('Agregar')"
          >
            <i
              class="fa fa-plus-circle"
              aria-hidden="true"
            ></i>
          </b-button>
          <b-button
            @click="cargarContenido('Editar')"
            variant="warning"
            :disabled="emision.id == undefined"
          >
            <i class="fas fa-edit "></i>
          </b-button>
          <b-button
            variant="danger"
            @click="confirmacionEliminar()"
            :disabled="emision.id == undefined"
          >
            <i
              class="fa fa-trash"
              aria-hidden="true"
            ></i>
          </b-button>
        </b-button-group>
      </b-navbar-nav>
    </b-navbar>
  </div>
</template>

<script>
import ListaContenido from "@/components/ListaContenido.vue";
import { mapActions, mapGetters, mapMutations } from "vuex";
import notificacion from "@/mixin/notificacion";
export default {
  name: "NavContenidoEmision",
  mixins: [notificacion],
  components: {
    ListaContenido
  },
  props: {
    tipo: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      catalogo: [],
      modo: "",
      gestionEmision: false
    };
  },
  computed: {
    ...mapGetters("Catalogo", ["contenido"]),
    ...mapGetters("emisiones", ["emision"]),
    getEmision() {
      return {
        id_contenido: this.contenido.id,
        tipo: this.getTipo,
        id: this.emision.id
      };
    },
    getTipo() {
      switch (this.tipo) {
        case "Estrenos":
          return 0;
        case "Series":
          return 1;
        case "Contemporaneas":
          return 2;
        default:
          return 2;
      }
    }
  },
  methods: {
    ...mapActions("Catalogo", ["cargarPeliculas", "cargarSeries"]),
    ...mapMutations("Catalogo", ["updateSelContenido"]),
    ...mapActions("emisiones", [
      "insertEmision",
      "loadEmisionesTipo",
      "loadEmisionExistente",
      "deleteEmision",
      "modifyEmision"
    ]),
    async cargarContenido(modo) {
      this.modo = modo;
      switch (this.tipo) {
        case "Estrenos":
          this.catalogo = await this.cargarPeliculas(0);
          break;
        case "Series":
          this.catalogo = await this.cargarSeries();
          break;
        case "Contemporaneas":
          this.catalogo = await this.cargarPeliculas(1);
          break;
        default:
          break;
      }
      if (modo === "Editar") {
        this.updateSelContenido(this.emision.id_contenido);
      }
      this.gestionEmision = !this.gestionEmision;
    },
    async agregarEmision() {
      const comprobacion = await this.loadEmisionExistente(
        this.getEmision.id_contenido
      );
      if (!comprobacion.error) {
        if (parseInt(comprobacion.cant.cant) > 0)
          this.show({ error: true }, "emision porque esta repetida", "agregar");
        else {
          const result = await this.insertEmision(this.getEmision);
          if (!result.error) {
            for (let i = 0; i <= 2; i++) {
              await this.loadEmisionesTipo(i);
            }
          }
          this.show(result, "emision", "agregada");
        }
      }
    },
    async modificarEmision() {
      const comprobacion = await this.loadEmisionExistente(
        this.getEmision.id_contenido
      );
      if (!comprobacion.error) {
        if (parseInt(comprobacion.cant.cant) > 0)
          this.show({ error: true }, "emision porque esta repetida", "modificar");
        else {
          const result = await this.modifyEmision(this.getEmision);
          if (!result.error) {
            for (let i = 0; i <= 2; i++) {
              await this.loadEmisionesTipo(i);
            }
          }
          this.show(result, "Emision", "modificada");
        }
      }
    },
    async eliminarEmision() {
      const result = await this.deleteEmision(this.emision.id);
      if (!result.error) {
        for (let i = 0; i <= 2; i++) {
          await this.loadEmisionesTipo(i);
        }
      }
      this.show(result, "Emision", "eliminada");
    },
    async confirmacionEliminar() {
      const resultado = await this.$bvModal.msgBoxConfirm(
        "¿Desea eliminar la emision?",
        {
          title: "Confirme",
          size: "sm",
          buttonSize: "sm",
          okVariant: "danger",
          okTitle: "Si",
          cancelTitle: "No",
          footerClass: "p-2",
          hideHeaderClose: false,
          centered: true
        }
      );
      if (resultado) this.eliminarEmision();
    }
  },
  created() {}
};
</script>

<style lang="scss" scoped>
</style>