<template>
  <div>
    <NavContenidoEmision :tipo="tipo" />
    <b-container
      fluid
      class="p-2"
    >
      <b-row>
        <b-col
          md="3"
          v-for="(Emision, index) in emisiones"
          :key="index"
          @click="seleccionarEmision(Emision)"
        >
          <ContenidoIndividual
            :class="[emision.id === Emision.id ? 'shadow-lg' : 'shadow-sm', emision.id === Emision.id ? 'border-success' : '', emision.id === Emision.id ? 'bg-light' : '']"
            :id="Emision.id_contenido"
          />
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import ContenidoIndividual from "@/components/ContenidoIndividual.vue";
import NavContenidoEmision from "@/components/ContenidoEmisionComp/NavContenidoEmision.vue";
import { mapMutations, mapGetters } from "vuex";

export default {
  name: "ContenidoEmisionEspecifico",
  components: {
    ContenidoIndividual,
    NavContenidoEmision
  },
  props: {
    tipo: {
      type: String,
      default: ""
    },
    emisiones: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    ...mapGetters("emisiones", ["emision"])
  },
  methods: {
    ...mapMutations("emisiones", ["updateEmision"]),
    seleccionarEmision(emision) {
      this.updateEmision(emision);
    }
  }
};
</script>

